.cds--side-nav--ux .cds--side-nav__items {
  width: fit-content;
  text-align: left;
  padding-bottom: 6rem; // ensure scrollable
}

.cds--side-nav.expanded {
  width: 16rem;
}

.cds--side-nav.rail {
  width: 3rem;
  overflow: visible;
}

// hide scrollbar for chrome, other webkit browsers (safari, opera)
.cds--side-nav__items::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.cds--side-nav__items {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.nav-popover-obj { // make the container wide enough for text
  width: 25rem;
}

.cds--side-nav__overlay-active {
    width: 100vw;
    height: 100vh;
    background-color: rgba(22, 22, 22, 0.5);
    background-color: var(--cds-overlay, rgba(22, 22, 22, 0.5));
    opacity: 1;
    transition: opacity 300ms cubic-bezier(0.5, 0, 0.1, 1), background-color 300ms cubic-bezier(0.5, 0, 0.1, 1);
}

a.cds--side-nav__link {
  outline: none !important;
  border: none !important;
}

