// Target direct div beneath the link and then the svg
.cds--side-nav__link.whiteFill > div > svg {
  fill: white;
}

// Target spans beneath to set color of text to white
.cds--side-nav__link.whiteFill span {
  color: white !important; // necessary to override default hovering
}

.cds--side-nav__divider.railDivider {
    width: 4.5rem;
    margin-left: -20px;
}

.cds--side-nav__divider.expandedDivider {
